import './List.css';

import Element from '../Element/Element';

function List({ items, deleteItem, getItem, onEdit }) {
    /**   map           Element,
        ,      */
    const listElements = items.map((item) => {
        return (
            <Element key={item.itemId}
            item={item}
            deleteItem={deleteItem}
            onEdit={onEdit}
            getItem={getItem}
            />
        )
    });

    return (
        <ul className='list'>
            {listElements}
        </ul>
    );
}

export default List;
